* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.8;
  color: #333;
  font-family: sans-serif;
}
h1 {
  font-weight: 700;
}
p {
  margin-bottom: 10px;
}

nav.header {
  background: rgb(255,170,170);
  background: linear-gradient(9deg, rgb(255, 105, 105) 0%, rgba(255,0,0,1) 100%);
