  color: white;
  padding: 0.5rem;
  a {
    margin-right: 15px;
    color: white;
    text-decoration: none;
    border-bottom: 1px solid transparent;
    padding-bottom: 3px;
    transition: border-bottom-color 300ms;
    font-weight: 100;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.2em;
    &:not(.active):hover, &.active {
      border-bottom-color: white;
    }
    &.active {
      cursor: default;
    }
  }
  span.logo {
    margin: 10px;
    cursor: default;
    font-size: 2em;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 100;
    margin-right: 5rem;
  }
}

section.body {
  padding: 1rem;
}